import React, { useEffect, useState } from 'react';
import { GatsbyImage } from 'gatsby-plugin-image';
import Box from '@mui/material/Box';

const SectionMainvisual = ({ images }) => {
  const [current, setCurrent] = useState(0);
  const [next, setNext] = useState(1);

  const [toggle, setToggle] = useState(false);

  useEffect(() => {
    // opacity animation
    const timer1 = setTimeout(() => {
      return;
      setToggle(!toggle);

      const timer2 = setTimeout(() => {
        clearTimeout(timer2);
      }, 1500);

      clearTimeout(timer1);
    }, 3000);
  }, [toggle]);

  return (
    <Box
      component='section'
      position='relative'
      sx={{
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        width: '100%',
        minHeight: '768px',
        height: '100vh',
        height: '100dvh'
      }}
    >
      <Box position='relative'>
        <Box sx={{display: {md: 'block', xs: 'none'}}}>
          <GatsbyImage
            image={images[0].childImageSharp.gatsbyImageData}
            alt='YUHEI ONO'
            layout='fullWidth'
            placeholder='blurred'
          />
        </Box>
        <Box sx={{display: {md: 'none'}}}>
          <GatsbyImage
            image={images[1].childImageSharp.gatsbyImageData}
            alt='YUHEI ONO'
            layout='fullWidth'
            placeholder='blurred'
          />
         </Box>
        {/*
        <GatsbyImage
          image={images[next].childImageSharp.gatsbyImageData}
          alt='YUHEI ONO'
          layout='fullWidth'
          placeholder='blurred'
          style={{
            ...AbsoluteFullStyle,
            transition: 'opacity 1500ms ease-out',
            opacity: toggle ? 1 : 0,
            zIndex: 10
          }}
        />
        */}
      </Box>
    </Box>
  );
};

export default SectionMainvisual;
